function getIndex(imageData, col, row) {
    return 4*row*imageData.width + 4*col;
}

export function getData(imageData, col, row) {
    const index = getIndex(imageData, col, row);
    return [imageData.data[index], imageData.data[index+1], imageData.data[index+2], imageData.data[index+3]];
}

export function setData(imageData, col, row, [r, g=r, b=r, a=255]) {
    const index = getIndex(imageData, col, row);
    imageData.data[index] = r;
    imageData.data[index+1] = g;
    imageData.data[index+2] = b;
    imageData.data[index+3] = a;
}

export function getBrightness(imageData, col, row, useAverage=false) {
    const index = getIndex(imageData, col, row);
    if(useAverage)
        return (imageData.data[index] + imageData.data[index+1] + imageData.data[index+2]) / 3;
    else
        return 0.34 * imageData.data[index] + 0.5 * imageData.data[index + 1] + 0.16 * imageData.data[index + 2];
}

export function getContext(canvas) {
    return canvas.getContext("2d");
}

export function download(canvas, name="unnamed") {
    const image = canvas.toDataURL("image/png");
    const aLink = document.createElement('a');
    aLink.download = name+'.png';
    aLink.href = image;
    aLink.click();
}

export function getWrapper(canvas) {
    const context = getContext(canvas);
    const imageData = context.getImageData(0, 0, canvas.width, canvas.height);
    return {
        getData: getData.bind(this, imageData),
        setData: setData.bind(this, imageData),
        getBrightness: getBrightness.bind(this, imageData),
        download: download.bind(this, canvas),
        getContext() {
            return context;
        },
        save() {
            context.putImageData(imageData, 0, 0);
        },
        getWidth() {
            return canvas.width;
        },
        getHeight() {
            return canvas.height;
        }
    }
}


