import React from 'react';

import Well from '../containers/Well';
import LoadingScreen from '../views/LoadingScreen';

import DiscordProfile from '../views/DiscordProfile';

/*
States:
0 - nothing done yet
1 - session created (enable button)
2 - waiting for auth (auth window opened)
3 - authorized, but nothing fetched yet
4 - info fetched
5 - auth error
6 - general error (do not retry)
*/

class DiscordAuth extends React.Component {
    constructor() {
        super();
        this.state = {
            state: 0
        };

    }

    componentDidMount() {
        this._getSession((sess) => {
            this.setState({
                session: sess,
                state: 1
            });
        });
    }

    _getSession(cb) {
        fetch("https://"+window.location.hostname+"/api/discord/session", {method: 'POST'})
        .then((response) => {
            return response.json();
        })
        .then((sess) => {
            if(cb)
                cb(sess);
            else
                this.setState({session: sess});
        })
        .catch((error) => {
            console.error(error);
            this.setState({
                state: 6,
                error: "Could not create Session"
            });
        });
    }

    _fetchInfo() {
        fetch("/api/discord/data", {headers: { token: this.state.token }})
        .then((response) => response.json())
        .then(({error, user, connections}) => {
            if(error) {
                console.error("Error fetching data with token", error);
                this._getSession((sess) => this.setState({state: 5, session: sess}));
            } else {
                this.setState({
                    data: {user, connections},
                    state: 4
                });
            }
        });
    }

    _handleMessage(messageEvent) {
        if(messageEvent.origin !== window.location.origin) {
            this.setState({
                state: 6,
                error: "Something is not quite right"
            });
            return;
        }
        switch(messageEvent.data) {
            case "ok":
                fetch("/api/discord/session/"+this.state.session.id)
                .then((response) => {
                    return response.json()
                })
                .then((res) => {
                    if(res.error) {
                        console.error("Oauth error: "+res.error);
                        this._getSession((sess) => this.setState({state: 5, session: sess}));
                    } else {
                        this.setState({
                            ...res,
                            state: 3
                        });
                        this._fetchInfo();
                    }
                })
                .catch((error) => {
                    console.error(error);
                    this.setState({
                        state: 6,
                        error: "Could not contact server"
                    });
                });
                break;
            default:
                console.error("Got non-ok message from popup: ", messageEvent);
                this._getSession((sess) => this.setState({state: 5, session: sess}));
        }
    }

    _handleAuthButton() {
        window.addEventListener("message", (msg) => this._handleMessage(msg), {
            once: true,
            passive: true,
            capture: false
        });
        window.open(this.state.session.url, "Discord Auth", "width=500,height=700");
        this.setState({
            state: 2
        });
    }

    render() {
        return (
            <Well title="Discord Oauth Tool">
                <h3>Description:</h3>
                <p>
                    This tool is just there to test Discord Oauth. Once you Authorized this tool, Some user informations and your Connections will be shown.<br/>
                    You can additionally choose to join one of the enabled Guilds (currently none).<br/>
                    <b>Note: Nothing shown here is stored on my server.</b>
                </p>
                <div>
                    {(this.state.state === 0 || this.state.state === 1 || this.state.state === 5) && (<p>
                        {this.state.state === 5 
                            ? <b>There was an error authenticating... please try again</b>
                            : <span>To start, just press the "Authorize" button</span>
                        }
                        <br/>
                        <button 
                            type="button"
                            className="btn btn-md btn-primary"
                            onClick={() => this._handleAuthButton()}
                            disabled={this.state.state === 0}
                        >
                            Authorize
                        </button>
                    </p>)}
                    {this.state.state === 2 && <LoadingScreen text="Waiting for authorization..."/>}
                    {this.state.state === 3 && <LoadingScreen text="Getting data..."/>}
                    {this.state.state === 6 && <p>
                        <b>There was an unrecoverable error: {this.state.error}</b>
                    </p>}
                    {this.state.state === 4 && <div>
                        {this.state.data.user 
                            ? <DiscordProfile {...this.state.data.user} /> 
                            : <h3>User informations not granted</h3>
                        }
                        {this.state.data.connections 
                            ? <div>
                                <h3>Connections:</h3>
                                <table className="table">
                                    <thead>
                                        <tr>
                                            <th scope="col">Connection</th>
                                            <th scope="col">Name</th>
                                            <th scope="col">Shown on profile?</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {Object.entries(this.state.data.connections).map(([conn, val]) => (
                                            <tr key={conn}>
                                                <td>{conn}</td>
                                                <td>{val.name}</td>
                                                <td>{val.visibility === 0 ? "No" : "Yes"}</td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>
                              </div>
                            : <h3>Connection informations not granted</h3>
                        }
                    </div>}
                </div>
            </Well>
        );
    }
}

export default DiscordAuth;


