import React from 'react';
import PropTypes from 'prop-types';

import { Link } from 'react-router-dom';

const publicPath = process.env.PUBLIC_URL;

class LinkDialog extends React.Component {
    static propTypes = {
        links: PropTypes.object.isRequired,
        hideLinks: PropTypes.bool,
        linkAttributes: PropTypes.object
    };

    static defaultProps = {
        hideLinks: true
    };

    constructor(props) {
        super(props);

        this._showLinks = this._showLinks.bind(this);
        this._toggleLinks = this._toggleLinks.bind(this);

        let array = [];
        for(let key in props.links) {
            if(props.links.hasOwnProperty(key)) {
                let url = props.links[key];
                if(url.indexOf('http') === 0 || url.indexOf('/') === 0) {
                    array.push((
                        <a key={'dlLink_' + props.links[key]} href={url} {... this.props.linkAttributes}
                           onClick={this._toggleLinks}>{key}</a>
                    ));
                } else {
                    let localUrl = publicPath + '/' + url;
                    if(url.indexOf('dl/') === 0) {
                        array.push((
                            <a key={'dlLink_' + props.links[key]} href={localUrl} {... this.props.linkAttributes}
                               onClick={this._toggleLinks}>{key}</a>
                        ));
                    } else {
                        array.push((
                            <Link key={'dlLink_' + props.links[key]} to={localUrl} {... this.props.linkAttributes}
                                  onClick={this._toggleLinks}>{key}</Link>
                        ));
                    }
                }
                array.push(" ");
            }
        }
        array.pop();

        this.state = {
            linksShown: !props.hideLinks,
            linkTags: array
        };
    }

    render() {
        if(this.state.linksShown) {
            return <span>{this.state.linkTags}</span>;
        } else {
            return <a href="#" onClick={this._showLinks}>Show Link(s)</a>;
        }
    }

    _showLinks(ev) {
        ev.preventDefault();
        this._toggleLinks();
    }

    _toggleLinks() {
        if(this.props.hideLinks) {
            this.setState({
                linksShown: !this.state.linksShown
            })
        }
    }
}

export default LinkDialog;


