import React from 'react';

import DocTitle from '../components/DocTitle';
import NavBar from './NavBar';

export default () => (
    <div>
        <DocTitle title="Not Found"/>
        <NavBar/>
        <div className="container">
            <div className="text-danger text-center" style={{ marginTop: 75 }}>
                <h1>You made a BooBoo!</h1>
                <h3>The site you tried to visit does not exist!</h3>
            </div>
        </div>
    </div>
);


