import React from 'react';
import {getWrapper} from '../util/canvasUtils';

import PageContainer from '../containers/PageContainer';

class Image extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            showPreview: false,
            showResults: false,
            useClassicBrightness: false,
            brightnessSwitch: 0.5,
            useTransparency: false,
            maxWidth: 400
        };

        this._handleInput = this._handleInput.bind(this);
        this._handleChange = this._handleChange.bind(this);
        this._calcCiphers = this._calcCiphers.bind(this);
        this._storeCiphers = this._storeCiphers.bind(this);
        this._updateWindowDimensions = this._updateWindowDimensions.bind(this);
    }

    componentDidUpdate(_, prevState) {
        if(prevState.showPreview && this.state.showPreview) {
            if(prevState.useClassicBrightness !== this.state.useClassicBrightness
                    || prevState.brightnessSwitch !== this.state.brightnessSwitch) {
                this._calcBlackWhite();
            }
        }
    }

    componentDidMount() {
        this._updateWindowDimensions();
        window.addEventListener('resize', this._updateWindowDimensions);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this._updateWindowDimensions);
    }

    _updateWindowDimensions() {
        const oldMax = this.state.maxWidth;
        const newMax = window.innerWidth - 100;
        if(newMax < 400) {
            this.setState({
                maxWidth: newMax
            });
        } else if(this.state.maxWidth < 400) {
            this.setState({
                maxWidth: 400
            });
        } else {
            return;
        }
        //redraw if needed
        if(!this.state.showPreview)
            return;
        if(this.origCanvas.width > newMax || this.state.image.width > oldMax) {
            this._drawPreviews();
            if(this.state.showResults) {
                this._calcCiphers();
            }
        }
    }

    _calcBlackWhite() {
        const originalW = getWrapper(this.origCanvas);
        const width = originalW.getWidth();
        const height = originalW.getHeight();

        this.bwCanvas.width = width;
        this.bwCanvas.height = height;
        const canvasW = getWrapper(this.bwCanvas);

        const useAvg = this.state.useClassicBrightness;

        for(let row = 0; row < height; row +=2) {
            for(let col = 0; col < width; col +=2) {
                const brightness = (originalW.getBrightness(col, row, useAvg)+originalW.getBrightness(col+1, row, useAvg)
                    +originalW.getBrightness(col+1, row+1, useAvg)+originalW.getBrightness(col, row+1, useAvg))/4;
                let output = 0;
                if(brightness > 255 * this.state.brightnessSwitch) {
                    output = 255;
                }

                canvasW.setData(col, row, [output]);
                canvasW.setData(col+1, row, [output]);
                canvasW.setData(col+1, row+1, [output]);
                canvasW.setData(col, row+1, [output]);
            }
        }

        canvasW.save();
    }

    _drawPreviews(image = this.state.image) {
        let width = image.width;
        let height = image.height;
        if(width > this.state.maxWidth) {
            height = Math.floor(height * this.state.maxWidth / width);
            width = this.state.maxWidth;
        }
        width -= width%2;
        height -= height%2;

        const canvas = this.origCanvas;
        canvas.width = width;
        canvas.height = height;
        const ctx = canvas.getContext("2d");
        ctx.fillStyle = "#FFFF";
        ctx.fillRect(0, 0, width, height);
        ctx.drawImage(image, 0, 0, width, height);

        this._calcBlackWhite();

        this.setState({
            showPreview: true,
            showResults: false,
        });
    }

    _handleInput() {
        if(this.fileInput.files.length > 0) {
            const file = this.fileInput.files[0];
            if(file.type.indexOf("image/") !== 0)
                return;
            console.log("loading image");
            const fr = new FileReader();
            fr.onload = () => {
                const image = document.createElement("img");
                image.onload = () => {
                    this.setState({
                        image: image
                    });
                    this._drawPreviews(image);
                    console.log("Done");
                };
                image.onerror = () => {
                    console.error("Image failed");
                };
                image.src = fr.result;
            };
            fr.readAsDataURL(file);
        }
    }

    _handleChange(event) {
        if(event.target === this.bwSlider) {
            if(this.state.brightnessSwitch !== this.bwSlider.value) {
                this.setState({
                    brightnessSwitch: this.bwSlider.value
                });
            }
        } else if(event.target === this.checkTrans) {
            this.setState({
                useTransparency: !this.state.useTransparency
            })
        } else if(event.target.name === "brightnessCalc") {
            const bool = event.target.value === "average";
            if(this.state.useClassicBrightness !== bool) {
                this.setState({
                    useClassicBrightness: bool
                });
            }
        }
    }

    _calcCiphers() {
        if(!this.state.showPreview)
            return;
        const bwCanvasW = getWrapper(this.bwCanvas);
        const width = bwCanvasW.getWidth();
        const height = bwCanvasW.getHeight();

        const cipher1Canvas = this.cipher1Canvas;
        const cipher2Canvas = this.cipher2Canvas;

        cipher1Canvas.width = cipher2Canvas.width = width;
        cipher1Canvas.height = cipher2Canvas.height = height;

        const cipher1W = getWrapper(cipher1Canvas);
        const cipher2W = getWrapper(cipher2Canvas);

        const whiteData = this.state.useTransparency ? [255, 255, 255, 0] : [255];

        for(let row = 0; row < height; row +=2) {
            for(let col = 0; col < width; col +=2) {
                const isBlack = bwCanvasW.getData(col, row)[0] === 0;
                const rand = Math.random() < 0.5;
                if(isBlack) {
                    if(rand) {
                        cipher1W.setData(col, row, whiteData);
                        cipher1W.setData(col+1, row, [0]);
                        cipher1W.setData(col+1, row+1, whiteData);
                        cipher1W.setData(col, row+1, [0]);

                        cipher2W.setData(col, row, [0]);
                        cipher2W.setData(col+1, row, whiteData);
                        cipher2W.setData(col+1, row+1, [0]);
                        cipher2W.setData(col, row+1, whiteData);
                    } else {
                        cipher1W.setData(col, row, [0]);
                        cipher1W.setData(col+1, row, whiteData);
                        cipher1W.setData(col+1, row+1, [0]);
                        cipher1W.setData(col, row+1, whiteData);

                        cipher2W.setData(col, row, whiteData);
                        cipher2W.setData(col+1, row, [0]);
                        cipher2W.setData(col+1, row+1, whiteData);
                        cipher2W.setData(col, row+1, [0]);
                    }
                } else {
                    if(rand) {
                        cipher1W.setData(col, row, whiteData);
                        cipher1W.setData(col+1, row, [0]);
                        cipher1W.setData(col+1, row+1, whiteData);
                        cipher1W.setData(col, row+1, [0]);

                        cipher2W.setData(col, row, whiteData);
                        cipher2W.setData(col+1, row, [0]);
                        cipher2W.setData(col+1, row+1, whiteData);
                        cipher2W.setData(col, row+1, [0]);
                    } else {
                        cipher1W.setData(col, row, [0]);
                        cipher1W.setData(col+1, row, whiteData);
                        cipher1W.setData(col+1, row+1, [0]);
                        cipher1W.setData(col, row+1, whiteData);

                        cipher2W.setData(col, row, [0]);
                        cipher2W.setData(col+1, row, whiteData);
                        cipher2W.setData(col+1, row+1, [0]);
                        cipher2W.setData(col, row+1, whiteData);
                    }
                }
            }
        }

        cipher1W.save();
        cipher2W.save();

        this.setState({
            showResults: true
        })
    }

    _storeCiphers() {
        getWrapper(this.cipher1Canvas).download("cipher1");
        getWrapper(this.cipher2Canvas).download("cipher2");
    }

    render() {
        return (
            <PageContainer title="Visual Cryptography">
                <h3>Description</h3>
                <p>
                    This tool takes an image from the user, translates it into black-white
                    and splits it into two seemingly random cipher images. When these two cipher images are overlayed,
                    the black-white version of the original image gets revealed.
                </p>
                <p>Idea from <a href="http://www.datagenetics.com/blog/november32013/" target="_blank">Datagenetics.com</a></p>
                <h3>Try it</h3>
                <label className="btn btn-default">
                    <span className="glyphicon glyphicon-folder-open" aria-hidden="true"/>
                    &nbsp;Select your Image
                    <input type="file" name="Image" accept="image/*" onChange={this._handleInput} ref={input => this.fileInput = input} style={{display: 'none'}}/>
                </label>
                <div style={{marginTop: 10, display: (this.state.showPreview ? 'block' : 'none')}}>
                    <h4>Original</h4>
                    <canvas className="media-object" ref={canvas => this.origCanvas = canvas}/>
                    <h4>Black-White version</h4>
                    <canvas className="media-object" ref={canvas => this.bwCanvas = canvas}/>
                    <label htmlFor="brightnessSlider">Black/White Threshold: {this.state.brightnessSwitch}</label>
                    <input
                        type="range"
                        name="brightnessSlider"
                        min={0}
                        max={1}
                        step={0.05}
                        defaultValue={this.state.brightnessSwitch}
                        style={{maxWidth: 200}}
                        onChange={this._handleChange}
                        ref={input => this.bwSlider = input}
                    />
                    <label htmlFor="brightnessCalc">Brightness-Calculation</label>
                    <div>
                        <label className="radio-inline">
                            <input
                                type="radio"
                                name="brightnessCalc"
                                value="colorWeighted"
                                checked={this.state.useClassicBrightness === false}
                                onChange={this._handleChange}
                            /> Color Weighted
                        </label>
                        <label className="radio-inline">
                            <input
                                type="radio"
                                name="brightnessCalc"
                                value="average"
                                checked={this.state.useClassicBrightness === true}
                                onChange={this._handleChange}
                            /> Average
                        </label>
                    </div>
                    <br/>
                    <label>
                        <input type="checkbox" checked={this.state.useTransparency} ref={check => this.checkTrans = check} onChange={this._handleChange}/>
                        Use Transparency
                    </label>
                    <br/>
                    <button className="btn btn-success" onClick={this._calcCiphers}>Calculate Cipher</button>
                </div>
                <div style={{marginTop: 10, display: (this.state.showResults ? 'block' : 'none')}}>
                    <canvas ref={canvas => this.cipher1Canvas = canvas} />&nbsp;
                    <canvas ref={canvas => this.cipher2Canvas = canvas} />
                    <p className="text-muted">Overlaying these 2 images reveals the original</p>
                    <button className="btn btn-info" onClick={this._storeCiphers}>Download as Images</button>
                </div>
            </PageContainer>
        )
    }
}

export default Image;


