import React from 'react';
import PropTypes from 'prop-types';
import { withRouter, Link } from 'react-router-dom';

import $ from 'jquery';

const navEntries = [
    {
        name: "Home",
        to: "/"
    },
    {
        name: "Discord",
        to: "/discord"
    },
//    {
//        name: "HsR2D",
//        to: "/hsr2d"
//    },
    {
        name: "Pw",
        to: "/pw"
    },
    {
        name: "Img",
        to: "/image"
    },
    {
        name: "Misc.Tools",
        to: "/misc"
    },
    {
        name: "NiX-Clan",
        to: "/clan"
    },
    {
        name: "Contact",
        to: "/contact",
        right: true
    }
];

const publicPath = process.env.PUBLIC_URL;

const NavLink = ({ name, to, active }) => (
    <li className={active ? 'active' : ''} onClick={() => $("#navNav").removeClass("in")}>
        <Link to={to}>
            {name}
            {active && <span className="sr-only">&nbsp;(current)</span>}
        </Link>
    </li>
);

NavLink.propTypes = {
    name: PropTypes.string.isRequired,
    to: PropTypes.string.isRequired,
    active: PropTypes.bool.isRequired
};

const NavBar = withRouter(({location}) => {
    const fixedEntries = navEntries.map(({name, to, right}) => {
        if(to[0] === '/') {
            to = publicPath + to;
        }
        return {
            name: name,
            to: to,
            right: right,
            active: (to === location.pathname)
        };
    });
    return (
        <nav className="navbar navbar-default navbar-fixed-top">
            <div className="container">
                <div className="navbar-header">
                    <button type="button" className="navbar-toggle" onClick={() => $("#navNav").toggleClass("in")}>
                        <span className="sr-only">Toggle navigation</span>
                        <span className="icon-bar"/>
                        <span className="icon-bar"/>
                        <span className="icon-bar"/>
                    </button>
                </div>
                <div id="navNav" className="collapse navbar-collapse">
                    <ul className="nav navbar-nav">
                        {fixedEntries.filter(({right}) => !right).map(
                            ({name, to, active}) => <NavLink name={name} key={name} to={to} active={active} />
                        )}
                    </ul>
                    <ul className="nav navbar-nav navbar-right">
                        {fixedEntries.filter(({right}) => right).map(
                            ({name, to, active}) => <NavLink name={name} key={name} to={to} active={active} />
                        )}
                    </ul>
                </div>
            </div>
        </nav>
    );
});

export default NavBar;


