import React from 'react';
import PropTypes from 'prop-types';

import CryptoJs from 'crypto-js';
import { Route, Link, Redirect } from 'react-router-dom';

import PageTitle from './PageTitle';
import DocTitle from './DocTitle';

const config = {
    navigationDelay: 7000,
    textLabel: "Text to protect",
    textHint: "Just write something already!",
    linkLabel: "Link to protect",
    linkHint: "http://www.example.com"
};

const PasswordForm = ({ title, pageTitle, errorMessage, successMessage, successTitle, onSubmit, buttonText }) => {
    const handleSubmit = (event) => {
        event.preventDefault();
        onSubmit(event.target.password.value);
    };
    return (
        <div className="container" style={{ maxWidth: '750px' }}>
            {pageTitle && <DocTitle title="Resource is protected"/>}
            <h1>{title}</h1>
            {errorMessage && <div className="alert alert-danger" role="alert"><b>{errorMessage}</b></div>}
            {successMessage && <div className="alert alert-success" role="alert" style={{ overflowWrap: 'break-word' }}>
                {successTitle && <b>{successTitle}<br/></b>}
                {successMessage}
            </div>}
            <form onSubmit={handleSubmit}>
                <div className="form-group">
                    <label htmlFor="password">Password</label>
                    <input
                        type="text"
                        id="password"
                        className="form-control"
                        placeholder="Password"
                    />
                </div>
                <button type="submit" className="btn btn-default">{buttonText}</button>
            </form>
        </div>
    );
};

PasswordForm.propTypes = {
    title: PropTypes.string.isRequired,
    onSubmit: PropTypes.func.isRequired,
    buttonText: PropTypes.string,
    pageTitle: PropTypes.string,
    errorMessage: PropTypes.string,
    successMessage: PropTypes.string,
    successTitle: PropTypes.string
};

PasswordForm.defaultProps = {
    buttonText: 'Submit'
};

class ProtectionPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dataLabel: config.linkLabel,
            dataHint: config.linkHint
        };

        this._handleSubmit = this._handleSubmit.bind(this);
        this._handleTypeChange = this._handleTypeChange.bind(this);
    }

    _handleSubmit(event) {
        event.preventDefault();
        const form = event.target;
        const type = form.type.value;
        const data = form.data.value;
        const password = form.password.value;

        if(data && data.trim().length > 0 && password && password.trim().length > 0) {
            let encrypted = CryptoJs.AES.encrypt(
                JSON.stringify({
                    type: type,
                    data: data
                }),
                password);
            let token = encrypted.toString(CryptoJs.enc.base64).replace(/\+/g, '-').replace(/\//g, '_');
            this.setState({
                token: token,
                error: undefined
            });
        } else {
            this.setState({
                error: 'Please fill out all fields!',
                token: undefined
            });
        }
    }

    _handleTypeChange(event) {
        event.preventDefault();
        if(event.target.value === 'text') {
            this.setState({
                dataLabel: config.textLabel,
                dataHint: config.textHint
            });
        } else {
            this.setState({
                dataLabel: config.linkLabel,
                dataHint: config.linkHint
            });
        }
    }

    render() {
        return (
            <div className="container" style={{ maxWidth: '750px' }}>
                <PageTitle title="Password Protector"/>
                {this.state.error && <div className="alert alert-danger" role="alert"><b>{this.state.error}</b></div>}
                {this.state.token &&
                <div className="alert alert-success" role="alert" style={{ overflowWrap: 'break-word' }}>
                    <b>Protected successfully! Use following link to share:</b><br/>
                    <Link to={'pw/' + this.state.token}>{window.location.href + '/' + this.state.token}</Link>
                </div>}
                <form onSubmit={this._handleSubmit}>
                    <div className="form-group">
                        <label htmlFor="type">Type</label>
                        <select
                            id="type"
                            className="form-control"
                            onChange={this._handleTypeChange}
                        >
                            <option value="link">Link</option>
                            <option value="text">Text</option>
                        </select>
                    </div>
                    <div className="form-group">
                        <label htmlFor="data">{this.state.dataLabel}</label>
                        <input
                            type="text"
                            id="data"
                            className="form-control"
                            placeholder={this.state.dataHint}
                        />
                    </div>
                    <div className="form-group">
                        <label htmlFor="password">Password</label>
                        <input
                            type="text"
                            id="password"
                            className="form-control"
                            placeholder="Password"
                        />
                    </div>
                    <button type="submit" className="btn btn-default">Protect</button>
                </form>
                <br/>
                <p className="text-center text-muted">Note: This uses AES with a 256-bit key derived from the
                    password.</p>
            </div>
        );
    }
}

class DecryptionPage extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            message: props.match.params.enc
        };

        this._navigate = this._navigate.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    componentDidUpdate() {
        let newMessage = this.props.match.params.enc;
        if(newMessage !== this.state.message) {
            this._cancelNavigation();
            this.setState({
                message: newMessage,
                error: undefined,
                result: undefined
            });
        }
    }

    componentWillUnmount() {
        this._cancelNavigation();
    }

    _cancelNavigation() {
        if(this.timeout) {
            clearTimeout(this.timeout);
            this.timeout = false;
        }
    }

    _navigate() {
        this.timeout = false;
        if(this.state.url.indexOf("http") === 0) {
            location.href = this.state.url;
        } else {
            this.setState({
                doRedirect: true
            });
        }
    }

    handleSubmit(password) {
        console.log("hi");
        if(password && password.trim().length > 0) {
            let raw = this.state.message.replace(/-/g, '+').replace(/_/g, '/');
            try {
                let decrypted = CryptoJs.AES.decrypt(raw, password);
                let object = JSON.parse(decrypted.toString(CryptoJs.enc.Utf8));
                if(object.type === 'text') {
                    this.setState({
                        result: object.data,
                        error: undefined
                    });
                } else {
                    this.setState({
                        url: object.data,
                        result: 'You will be redirected to ' + object.data + ' soon...',
                        error: undefined
                    });
                    this.timeout = setTimeout(this._navigate, config.navigationDelay);
                }
            } catch(error) {
                this.setState({
                    error: 'Access denied. Password may be wrong',
                    result: undefined
                });
            }
        } else {
            this.setState({
                error: 'Please specify the password',
                result: undefined
            });
        }
    }

    render() {
        return (
            <div>
                {this.state.doRedirect && <Redirect to={this.state.url}/>}
                <PasswordForm
                    title="Resource is protected"
                    onSubmit={this.handleSubmit}
                    pageTitle="Resource is protected"
                    errorMessage={this.state.error}
                    successMessage={this.state.result}
                    successTitle="Access granted!"
                    buttonText="Access Resource"
                />
            </div>
        );
    }
}

const PasswordPage = ({ match }) => (
    <div>
        <Route path={match.url + "/"} exact component={ProtectionPage}/>
        <Route path={match.url + "/:enc"} component={DecryptionPage}/>
    </div>
);

export { PasswordForm }

export default PasswordPage;


