import React from 'react';
import { render }  from 'react-snapshot';
import { BrowserRouter, Switch, Route } from 'react-router-dom';

import NavBar from './views/NavBar';

import ListView from './containers/ListView';

import Home from './views/Home';
import Discord from './views/Discord';
import HSReplay from './components/HSReplay';
import PasswordPage from './components/PasswordPage';
import Image from './components/Image';
import Contact from './views/ContactPage';

import NotFound from './views/NotFound';

const publicPath = process.env.PUBLIC_URL;

const PageRouter = () => (
    <BrowserRouter>
        <div>
            <Route path={publicPath} render={({match}) => {
                const root = match.path === '/' ? '' : match.path;
                return (
                    <div>
                        <NavBar/>
                        <Switch>
                            <Route exact path={`${root}/`} component={Home}/>
                            <Route path={`${root}/discord`} component={Discord}/>
                            {/*<Route path={`${root}/hsr2d`} component={HSReplay}/>*/}
                            <Route path={`${root}/pw`} component={PasswordPage}/>
                            <Route path={`${root}/image`} component={Image}/>
                            <Route path={`${root}/misc`}>
                                <ListView title="Miscellaneous Tools" dataFile="tools.json" dataAge={6*3600}/>
                            </Route>
                            <Route path={`${root}/clan`}>
                                <ListView title="NiX-Clan stuff" dataFile="clan.json" dataAge={6*3600}/>
                            </Route>
                            <Route path={`${root}/contact`} component={Contact}/>
                            <Route component={NotFound}/>
                        </Switch>
                    </div>
                );
            }}/>
        </div>
    </BrowserRouter>
);

import './styles/bootstrap.min.css';
import './styles/common.css';

render(<PageRouter/>, document.querySelector('main'));


