import React from 'react';
import PropTypes from 'prop-types';

import Section from '../views/Section';
import PageTitle from '../components/PageTitle';

import resourceLoader from '../util/resourceLoader';

class ListView extends React.Component {
    static propTypes = {
        title: PropTypes.string,
        dataFile: PropTypes.string.isRequired,
        dataAge: PropTypes.number
    };

    constructor(props) {
        super(props);
        this.state = {
            sections: [],
            error: false,
            clicked: 0
        };
        this.handleTitleClick = this.handleTitleClick.bind(this);
        this._resetClicks = this._resetClicks.bind(this);
    }

    _fetchSections() {
        if(this.props.dataFile !== '') {

            resourceLoader.fetch('data/' + this.props.dataFile, this.props.dataAge ? this.props.dataAge : 0, (data, err) => {
                if(err) {
                    this.setState({
                        error: err.message
                    });
                } else {
                    this.setState({
                        sections: data
                    });
                }
            });
        }
    }

    _resetClicks() {
        if(this.state.clicked > 0) {
            this.setState({
                clicked: 0
            });
        }
    }

    handleTitleClick() {
        if(this.state.clicked === 4) {  //clicked 5 times
            resourceLoader.clearStorage();
        }
        this.setState({
            clicked: this.state.clicked + 1
        });
        setTimeout(this._resetClicks, 1000);
    }

    componentDidMount() {
        this._fetchSections();
    }

    componentDidUpdate(prevProps) {
        if(prevProps.dataFile !== this.props.dataFile)
            this._fetchSections();
    }

    render() {
        const title = this.props.title || 'Kantenkugel\'s Tools';
        return (
            <div className="container">
                <PageTitle title={title} onClick={this.handleTitleClick}/>
                {this.state.error && <h3 className="text-danger">There was an error loading Data:
                    <small>{this.state.error}</small>
                </h3>}
                {this.state.sections.map(sec => <Section key={sec.title} {...sec} />)}
            </div>
        );
    }
}

export default ListView;


