import React, {Component} from 'react';

import Well from '../containers/Well';

class DiscordSnowflake extends Component {
    constructor() {
        super();
        this.state = {
            first: '',
            second: '',
            validation: {
                first: '',
                second: ''
            },
            valid: {
                first: false,
                second: false
            }
        };
    }

    handleChange(input, event) {
        const inputText = event.target.value;
        const inputVal = +inputText;
        const isValid = !isNaN(inputVal) && inputText.length >= 16 && inputText.length <= 20;
        const validationClass = inputText === '' ? '' : (isValid ? ' has-success' : ' has-error');

        this.setState(({validation, valid}) => ({
            [input]: isValid ? inputVal : inputText,
            validation: {
                ...validation,
                [input]: validationClass
            },
            valid: {
                ...valid,
                [input]: isValid
            }
        }));
    }

    render() {
        let timeDiff;
        if(this.state.valid.first && this.state.valid.second)
            timeDiff = getDate(this.state.second).getTime() - getDate(this.state.first).getTime();
        
        return (
            <Well title="Snowflake Time">
                <div className="row">
                    <div className="col-12 col-md-6">
                        <div className={"form-group" + this.state.validation.first}>
                            <label htmlFor="firstSnowflake">Discord Snowflake</label>
                            <input
                                type="text"
                                id="firstSnowflake"
                                className="form-control"
                                value={this.state.first}
                                onChange={(e) => this.handleChange('first', e)}
                                placeholder="122758889815932930"
                            />
                        </div>
                        { this.state.valid.first && (
                            <h4>
                                {getDate(this.state.first).toLocaleString()}
                            </h4>
                        )}
                    </div>
                    <div className="col-12 col-md-6">
                        <div className={"form-group" + this.state.validation.second}>
                            <label htmlFor="secondSnowflake">Discord Snowflake</label>
                            <input
                                type="text"
                                id="secondSnowflake"
                                className="form-control"
                                value={this.state.second}
                                onChange={(e) => this.handleChange('second', e)}
                                placeholder="125227483518861312"
                            />
                        </div>
                        { this.state.valid.second && (
                            <h4>
                                {getDate(this.state.second).toLocaleString()}
                            </h4>
                        )}
                    </div>
                </div>
                { (timeDiff !== undefined && (
                    <div className="text-center">
                        <h3>Difference:</h3>
                        <h4>{formatDuration(timeDiff)}</h4>
                        <h5>({timeDiff}ms)</h5>
                    </div>
                )) || <div></div>}
            </Well>
        );
    }
}

function getDate(snowflake) {
    return new Date(rShiftLong(snowflake, 22)+1420070400000);
}

function rShiftLong(num, shiftCount = 1) {
    let out = num;
    for(let i=0; i<shiftCount; i++) {
        out /= 2;
    }
    return Math.floor(out);
}

function formatDuration(durInMs) {
    let outString = '';
    let secs = Math.floor(durInMs/1000);

    //weeks
    let tmp = Math.floor(secs / 604800);
    if(tmp > 0) {
      outString += tmp+"w ";
      secs %= 604800;
    }
    //days
    tmp = Math.floor(secs / 86400);
    if (tmp > 0) {
        outString += tmp+"d ";
      secs %= 86400;
    }
    //hours
    tmp = Math.floor(secs / 3600);
    if (tmp > 0) {
        outString += tmp+"h ";
      if (tmp > 0) {
        secs %= 3600;
      }
    }
    //minutes
    tmp = Math.floor(secs / 60);
    if (tmp > 0) {
        outString += tmp+"m ";
      if (tmp > 0) {
        secs %= 60;
      }
    }
    //seconds
    if (secs > 0) {
        outString += secs+"s ";
    }
    //ms (only include if > 0, don't show when intermed is enabled but ms# is 0)
    tmp = durInMs%1000;
    if (tmp > 0) {
        outString += tmp+"ms ";
    }
    //empty -> 0ms
    if (outString.length === 0) {
      return "0ms";
    }
    return outString.substring(0, outString.length-1);
}

export default DiscordSnowflake;


