import React from 'react';
import PropTypes from 'prop-types';

import getAvatar from '../util/avatarUtil';

const DiscordProfile = ({username, discriminator, id, avatar, mfa_enabled}) => (
    <div>
        <h3>User info:</h3>
        <table className="table">
            <thead>
                <tr>
                    <th scope="col">Username # Discriminator</th>
                    <th scope="col">ID</th>
                    <th scope="col">2FA Enabled</th>
                    <th scope="col">Avatar</th>
                </tr>
            </thead>
            <tbody>
                <tr>
                    <td>{username}#{discriminator}</td>
                    <td>{id}</td>
                    <td>{mfa_enabled ? "Yes" : "No"}</td>
                    <td><img src={getAvatar(id, avatar, discriminator)} className="img-circle" height="48" alt="profile"/></td>
                </tr>
            </tbody>
        </table>
    </div>
);

DiscordProfile.propTypes = {
    username: PropTypes.string.isRequired,
    discriminator: PropTypes.string.isRequired,
    id: PropTypes.string.isRequired,
    avatar: PropTypes.string,
    mfa_enabled: PropTypes.bool.isRequired,
};

export default DiscordProfile;


