import React from 'react';
import PropTypes from 'prop-types';

const Well = ({ title, children }) => (
    <div className="container">
        <h1>{title}</h1>
        <div className="well">
            {children}
        </div>
    </div>
);

Well.propTypes = {
    title: PropTypes.string.isRequired,
    children: PropTypes.oneOfType([
        PropTypes.element,
        PropTypes.arrayOf(PropTypes.element)
    ]).isRequired
};

export default Well;


