const defaultAvatars = [
    "6debd47ed13483642cf09e832ed0bc1b",
    "322c936a8c8be1b803cd94861bdfa868",
    "dd4dbc0016779df1378e7812eabaa04d",
    "0e291f67c9274a1abdddeb3fd919cbaa",
    "1cbd08c76f8af6dddce02c5138971129"
];

function getDefaultAvatarId(discriminator) {
    return defaultAvatars[discriminator % defaultAvatars.length];
}

function getDefaultAvatarUrl(discriminator) {
    return "https://discordapp.com/assets/" + getDefaultAvatarId(discriminator) + ".png";
}

function getAvatarUrl(userId, avatarId, discriminator) {
    if(avatarId) {
        return "https://cdn.discordapp.com/avatars/" + userId + "/" + avatarId + (avatarId.startsWith("a_") ? ".gif" : ".png") + "?size=256";
    } else {
        return getDefaultAvatarUrl(discriminator);
    }
}

export default getAvatarUrl;
export {
    getDefaultAvatarId,
    getDefaultAvatarUrl,
    getAvatarUrl
}


