import React from 'react';

import '../styles/forms.css';

import PageContainer from '../containers/PageContainer';

class HSReplay extends React.Component {
    constructor(props) {
        super(props);
        this.webhookValidator = /^https:\/\/(?:(?:ptb|canary)\.)?discordapp.com\/api\/webhooks\/(\d+)\/([a-zA-Z0-9_-]+)$/;

        this.state = {
            validation: "",
            options: {},
            webhook: {}
        };

        this.handleChange = this.handleChange.bind(this);
        this.getHsrWebhook = this.getHsrWebhook.bind(this);
    }

    handleChange(event) {
        if(event.target === this.discordhook) {
            if(this.discordhook.value === "") {
                this.setState({
                    validation: "",
                    webhook: {}
                });
            } else {
                let validation = this.webhookValidator.exec(this.discordhook.value);
                if(validation) {
                    this.setState({
                        validation: " has-success",
                        webhook: {
                            id: validation[1],
                            key: validation[2]
                        }
                    });
                } else {
                    this.setState({
                        validation: " has-error",
                        webhook: {}
                    });
                }
            }
        } else if(event.target.id.indexOf('o_') === 0) {
            let options = this.state.options;
            let optionKey = event.target.id.substring(2);
            if(event.target.value === "") {
                delete options[optionKey];
            } else {
                options[optionKey] = event.target.value;
            }
            this.setState({
                options: options
            });
        }
    }

    getHsrWebhook() {
        if('id' in this.state.webhook) {
            let params = "";
            for(let key in this.state.options) {
                if(this.state.options.hasOwnProperty(key)) {
                    params += '&' + key + '=' + this.state.options[key];
                }
            }
            if(params.length > 0) {
                params = '?' + params.substring(1);
            }
            return "https://www.kantenkugel.com/api/hswebhook/" + this.state.webhook.id + '/' + this.state.webhook.key + params;
        } else {
            return "";
        }
    }

    render() {
        return (
            <PageContainer title="HsReplay.net to Discord relay">
                <h3>Description:</h3>
                <p>This tool supplies a bridge between <a href="http://hsreplay.net" target="_blank">HsReplay.net</a>
                    &nbsp;and Discord and will post your completed Hearthstone matches to Discord.<br/>
                <b>Note:</b> Your <a href="https://hsreplay.net/downloads/" target="_blank">Hearthstone Deck
                Tracker</a> has to be configured to upload replays to HsReplay.net</p>

                <h3>Usage:</h3>
                <p>First, <b>create a Webhook in Discord</b>, if you don't have one already. You can do this in the
                Channel or Server settings.</p>
                <div className={"form-group" + this.state.validation}>
                    <label htmlFor="discordhook">Discord Webhook URL</label>
                    <input
                        type="text"
                        id="discordhook"
                        className="form-control"
                        onChange={this.handleChange}
                        placeholder="https://discordapp.com/api/webhooks/XXXX/YYYYYYYYY"
                        ref={(input) => this.discordhook = input}
                    />
                </div>
                <br/>
                <p>Then configure the Webhook with these optional <b>options</b> to filter out which games you want to
                    post:</p>
                <div className="form-inline">
                    <div className="form-group">
                        <label htmlFor="o_gametype">Gametype</label>
                        <select id="o_gametype" className="form-control" onChange={this.handleChange}>
                            <option value="">All</option>
                            <option value="duel">Duel</option>
                            <option value="casual">Casual</option>
                            <option value="ranked">Ranked</option>
                            <option value="arena">Arena</option>
                            <option value="ai">AI</option>
                            <option value="brawl">Brawl</option>
                        </select>
                    </div>
                    <div className="form-group">
                        <label htmlFor="o_format">Format</label>
                        <select id="o_format" className="form-control" onChange={this.handleChange}>
                            <option value="">All</option>
                            <option value="standard">Standard</option>
                            <option value="wild">Wild</option>
                        </select>
                    </div>
                    <div className="form-group">
                        <label htmlFor="o_result">Result</label>
                        <select id="o_result" className="form-control" onChange={this.handleChange}>
                            <option value="">All</option>
                            <option value="win">Win</option>
                            <option value="loss">Loss</option>
                        </select>
                    </div>
                    <div className="form-group">
                        <label htmlFor="o_small">Condensed Output</label>
                        <select id="o_small" className="form-control" onChange={this.handleChange}>
                            <option value="">No</option>
                            <option value="true">Yes</option>
                        </select>
                    </div>
                    <div className="form-group">
                        <label htmlFor="o_finished">Finished Arena only</label>
                        <select id="o_finished" className="form-control" onChange={this.handleChange}>
                            <option value="">No</option>
                            <option value="true">Yes</option>
                        </select>
                    </div>
                    <div className="form-group">
                        <label htmlFor="o_username">Discord Username</label>
                        <input
                            type="text"
                            id="o_username"
                            className="form-control"
                            onChange={this.handleChange}
                            placeholder="Optional"
                        />
                    </div>
                </div>
                <br/>
                <p>Finally go to the <a href="https://hsreplay.net/account/api/">HsReplay.net api settings</a> and add
                a new Webhook with the following url:</p>
                <div className="form-group">
                    <label htmlFor="hsrhook">HsReplay.net Webhook URL</label>
                    <input
                        type="text"
                        id="hsrhook"
                        className="form-control"
                        readOnly="readOnly"
                        value={this.getHsrWebhook()}
                    />
                </div>
            </PageContainer>
        );
    }
}

export default HSReplay;


