import React from 'react';

import PageContainer from '../containers/PageContainer'

const Home = () => (
    <PageContainer title="Kantenkugel's Website">
        <h3>Welcome to my Website.</h3>
        <p>This is just a simple website I put together to get into React and to share stuff with my friends</p>
    </PageContainer>
);

export default Home;


