import React from 'react';

import PageContainer from '../containers/PageContainer';

const ContactPage = () => (
    <PageContainer title="Contact">
        <h3>How to contact me</h3>
        <h4>Discord</h4>
            <p>You can add me in Discord as <b>Kantenkugel#1568</b></p>
        <h4>Email</h4>
            <p>To send me emails, contact me at <a href="mailto:kantenkugel@kantenkugel.com">kantenkugel@kantenkugel.com</a></p>
        <h3>Other places you can find me</h3>
        <ul className="list-inline">
            <li><a href="http://www.github.com/kantenkugel" target="_blank">Github</a></li>
            <li><a href="http://steamcommunity.com/id/kanze/" target="_blank">Steam</a></li>
        </ul>
    </PageContainer>
);

export default ContactPage;


