import React from 'react';
import PropTypes from 'prop-types';
import LinkDialog from '../components/LinkDialog';

const Section = ({ title, version, desc, links, hideLinks, linkAttributes }) => (
    <div className="panel panel-default">
        <div className="panel-heading">
            <strong>
                {title}
                {version && <small>&nbsp;(v{version})</small>}
                :&nbsp;
            </strong>
            {links && <LinkDialog links={links} hideLinks={hideLinks} linkAttributes={linkAttributes}/>}
        </div>
        <div className="panel-body" dangerouslySetInnerHTML={{ __html: desc }}/>
    </div>
);

Section.propTypes = {
    title: PropTypes.string.isRequired,
    desc: PropTypes.string.isRequired,
    version: PropTypes.string,
    links: PropTypes.object,
    hideLinks: PropTypes.bool,
    linkAttributes: PropTypes.object
};

Section.defaultProps = {
    hideLinks: false
};

export default Section;


