import $ from 'jquery';

const localStorageKey = "siteCache_m";
const storageVersion = 1;

const pathPrefix = process.env.PUBLIC_URL + '/';

const cache = {};
let localStorageCache;
let localStorageAccessed = false;

function initLocalStorage() {
    if(localStorageAccessed) {
        return;
    }
    localStorageCache = localStorage.getItem(localStorageKey);
    if(localStorageCache === null) {
        console.log('No prior storage found');
        localStorageCache = {
            version: storageVersion,
            entries: {}
        };
    } else {
        localStorageCache = JSON.parse(localStorageCache);
    }
    if(localStorageCache.version < storageVersion) {
        console.log('Old storage version detected, clearing cache');
        localStorageCache = {
            version: storageVersion,
            entries: {}
        };
    }
    const currentTime = Math.floor(Date.now()/1000);
    for(let key in localStorageCache.entries) {
        if(localStorageCache.entries.hasOwnProperty(key)) {
            let entry = localStorageCache.entries[key];
            if(entry.validity && (entry.createdAt + entry.validity) < currentTime) {
                delete localStorageCache.entries[key];
            } else {
                cache[key] = entry.value;
            }
        }
    }
    localStorage.setItem(localStorageKey, JSON.stringify(localStorageCache));
    localStorageAccessed = true;
}

function store(key, value, validity) {
    cache[key] = value;
    if(validity !== 0) {
        let entry = {
            value: value,
            createdAt: Math.floor(Date.now() / 1000)
        };
        if(validity > 0)
            entry.validity = validity;
        localStorageCache.entries[key] = entry;
        localStorage.setItem(localStorageKey, JSON.stringify(localStorageCache));
    }
}

function clearStorage() {
    localStorageCache = {
        version: storageVersion,
        entries: {}
    };
    localStorage.setItem(localStorageKey, JSON.stringify(localStorageCache));
    console.log("Cleared local storage");
}

function fetch(path, storageAge, callback) {
    if(typeof storageAge === "function") {
        callback = storageAge;
        storageAge = 0;
    }
    if(storageAge !== 0 && !localStorageAccessed)
        initLocalStorage();
    if(path in cache) {
        callback(cache[path]);
    } else {
        let fetchPath;
        if(path.indexOf('/') === 0 || path.indexOf('http') === 0) {
            fetchPath = path;
        } else {
            fetchPath = pathPrefix + path;
        }
        $.get(fetchPath, data => {
            store(path, data, storageAge);
            callback(data);
        }, 'json').fail((obj, textStatus, errThrown) => {
            console.error('error getting resource:', path, textStatus, errThrown);
            callback(null, new Error(textStatus));
        });
    }
}

export default {
    fetch,
    clearStorage
}

export {cache, localStorageCache};


